import * as React from 'react';
import { Controller, RenderContentProps } from '@ali/4ever-cangjie';
import { LeftToolbar } from '@ali/4ever-component';
import { BiPluginConfig } from '../../types';
import {
  useScrollableContent,
} from '@ali/4ever-bamboo';

export default function createRenderContent(config: BiPluginConfig) {
  return (props: RenderContentProps, controller: Controller, next) => {
    return (
      <>
        {next()}
        {
          <LeftToolbar
            getScrollableContent={useScrollableContent}
            controller={controller}
            config={config.leftToolbar?.menu}
          />
        }
      </>
    );
  };
}
